html {
  font-size: 10px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', sans-serif;
}

.sub-heading {
  position: fixed;
  width: 100%;
  height: 75px;
  top: 40px;
  left: 0;
  overflow: hidden;
  z-index: 1000;
}

.sub-heading h2 {
  text-align: center;
  margin: 0;
  font-size: 2.8rem;
}

.bottle-btn {
  text-align: center;
  margin: 0 0 0 40px;
}

.bottle-btn button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 7px 0 9px 0;
  text-align: center;
  width: 120px;
  border: none;
  outline: none;
  background-color: #97dfb7;
  border-radius: 5px 15px 15px 5px;
  cursor: pointer;
}

.bottle-btn button span {
  font-size: 1.4rem;
  font-weight: bold;
  margin-left: -3px;
}

.bottle-btn div {
  display: inline-block;
  width: 40px;
  height: 18px;
  margin: 0 0 -4px -5px;
  border-radius: 0 5px 5px 0;
  background-color: #97dfb7;
}
.hero {
  background: url('/img/beer-pint.png') no-repeat center -15px;
  background-size: cover;
  min-height: 600px;       
}

.hero img {
  width: 30%;
  border-radius: 2em;
}

.hero-text {
  vertical-align: top;
  color: #090909;
  width: 50%;
  margin: 0 auto;
  padding: 13% 0 5% 0;
}

.hero-text p {
  font-size: 1.4rem;                
  text-align: center;
}

@media (min-width: 350px) {
  .hero {
      min-height: 700px;
  }
  .hero-text p {
    font-size: 1.6rem;
  }
}

@media (min-width: 768px) {
  .hero {
      width: 75%;
      margin: 0 auto;
      min-height: 1200px;
  }
  .hero-text p {
    font-size: 2rem;
  }
}
.beer-link, .beer-link:visited {
  text-decoration: none;
}

.content-heading {
  margin: 10px auto 0;    
  font-size: 1.6rem;
  font-weight: bold;
  text-align: center;
  color: #090909;;
  width: 75%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.error {
  color: #f52028;
}

.beer-listing {
  text-align: center;
  color: #f58220;
  width: 80%;
  margin: 3% auto;
  border-bottom: 1px solid #f58220;
}

.beer-listing .title-line {
  font-size: 1.8rem;
  padding: 0 0 1% 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.beer-lisitng .comment {
  font-size: 1.8rem;
  padding: 0 0 1% 0;
}

.beer-listing .detail-line {
  font-size: 1.4rem;
  padding: 0 0 1% 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.loader {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1;
  margin: -75px 0 0 -75px;
  border: 16px solid #e9e9e9;
  border-radius: 50%;
  border-top: 16px solid #78c1ce;;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

